            bp_ext: "pptx",
            bp_url: "20161201/e6b499c3e94ed4247147cdbfd50ca319.pptx",
            created_at: "2017-06-01 18:14:38",
            updated_at: "2017-06-01 18:14:38",
            deleted_at: null
          }

        },
        seachCompanys:[],
        styleObject: {
          color: '',
        }
      }
    },
    computed:{
    },
    components: {
      research
    },
    //Echart
